// gap
$gap: 8px;
$button-height-gap: 8px;

// color
$brand-y: #fcaf17;
$brand-g: #645b4c;

$secondary-green: #76b424;
$secondary-silver: #8e9090;
$secondary-gold: #8b6e4d;
$secondary-gold2: #776c61;
$secondary-black: #000;

$white: #fff;
$black: #000;
$red: #ff0000;
$blue: #22a2d1;

$bg-color-6: #645b4c;
$bg-color-f: #f6f7fa;
$bg-color-f5: #f5f5f5;
$bg-color-d: #d3d4d6;
$bg-color-77: #776c61;
$bg-color-ea: #eaeaea;

$gray-6: #666;
$gray-9: #999;
$gray-b: #bbb;
$gray-d: #ddd;
$gray-e: #e9e9e9;
$gray-6: #666;
$gray-a: #aeaeae;

$border-color-a: #acacac;
$border-color-d: #d3d4d6;
$border-color-e: #e6e6e8;
$border-color-7: #76b424;
$border-color-red: #ff0000;

// z-index
$ui-z-index: 100;
$header-z-index: 200;
$aside-z-index: 300;
$dialog-z-index: 400;
$toast-z-index: 500;
$tooltip-z-index: 50;
$popup-z-index: 450;
$menu-z-index: 190;

// transition
$base-duration: 0.3s;

// input
$input-height: 4.4rem;
$input-radius: 1rem;
$input-padding: 1.5rem;

// font
$kb: 'KB FG DISPLAY';
$noto: 'Noto Sans KR';

$defaultColor: $black;
$defaultBodyBg: $white;
$font: $noto;


// font
.kb-font {
  font-family: 'KB FG DISPLAY';
}

.noto-font {
  font-family: 'Noto Sans KR';
}

.point-y-font {
  color: $brand-y !important;
}

.point-b-font {
  color: $blue !important;
}

.point-r-font {
  color: $red !important;
}
