@import './base/fonts';

@import './base/variables';
@import './base/mixins';

html, body {
  background: #fff;
}

img {
  border: 0;
}

body, h1, h2, h3, h4, h5, h6, button, dl, dt, dd, p, ul, ol, li, legend, textarea, table, input, article, aside, footer, header, menu, nav, section, form, address {
  margin: 0;
  padding: 0;
}

article, aside, footer, header, menu, nav, section, address {
  display: block;
}

ul, ol, li {
  list-style: none;
}

.blind, .skip {
  overflow: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  z-index: -1;
  opacity: 0;
}

html {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  height: 100%;
  background: #fff;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: $noto, Helvetica, 'Apple SD Gothic Neo', Dotum, sans-serif;
  font-weight: 400;
  font-style: normal;

  * {
    box-sizing: border-box;
  }
}

button {
  appearance: none;
  border-radius: 0;
  border-width: 0;
  background: 0 0;
  outline: 0;
  font-family: $noto, Helvetica, 'Apple SD Gothic Neo', Dotum, sans-serif;
  font-weight: 400;
  font-style: normal;
  white-space: nowrap;

  &:focus {
    outline: 0;
  }
}

.page-certificate-copy {
  width: 1280px;
  margin: 0 auto;
  padding: 50px 60px;

  h1 {
    width: 80px;
    height: 80px;
    margin: 0 auto;
    line-height: 0;
  }

  .main-title {
    margin-top: 50px;
    font-family: $kb;
    font-size: 36px;
    font-weight: bold;
    line-height: 1.33;
    text-align: center;
    color: $black;
  }

  .sub-title {
    margin: 20px 0;
    font-size: 20px;
    line-height: 1.3;
    text-align: center;
    color: $gray-6;
  }
}

.content-sec {
  $root: &;

  padding: 60px 0;

  & + & {
    border-top: 1px solid $border-color-e;
  }

  &__title {
    margin-bottom: 30px;
    font-family: $kb;
    font-size: 24px;
    font-weight: bold;
    line-height: 1.5;
    text-align: left;
    color: $black;
  }
}

.content-text {
  display: flex;
  align-items: center;

  p {
    font-size: 16px;
    line-height: 1.38;
    text-align: left;
    color: $black;
    margin-right: 30px;

    span {
      color: $gray-6;
    }
  }

  button {
    height: 40px;
    padding: 8px 20px;
    background-color: #000000;
    font-size: 16px;
    text-align: center;
    color: #ffffff;
  }
}

.content-step {
  overflow: hidden;

  ul {
    display: flex;
    flex-wrap: wrap;
    margin: -40px -15px 0;
  }

  li {
    width: 50%;
    padding: 0 15px;
    margin-top: 40px;
  }

  p {
    margin-bottom: 15px;

    em {
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      line-height: 1.25;
      text-align: left;
      color: $black;
      margin-right: 15px;
    }

    span {
      font-size: 16px;
      line-height: 1.25;
      text-align: left;
      color: $gray-6;
    }
  }

  div {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 340px;
    border: 1px solid $border-color-d;
  }
}

.footer-sec {
  padding: 15px 0;
  border-top: 1px solid $border-color-e;
  font-size: 14px;
  line-height: 1.86;
  text-align: center;
  color: $gray-9;
}