/* noto-sans-kr-100 - latin_korean */
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 100;
  src:
    local(''),
    url('fonts/notosanskr/noto-sans-kr-v13-latin_korean-100.woff2') format('woff2'),
    url('fonts/notosanskr/noto-sans-kr-v13-latin_korean-100.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

