}

@mixin iconBoxTitle($icon: icon-explain) {
  padding-left: 5rem;
  position: relative;
  min-height: 4rem;
  flex: 0 0 auto;

  &__icon {
    position: absolute;
    left: 0;
    top: 0;
    border:1px solid #d3d4d6;
    border-radius: 1rem;
    width: 4rem;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &::after {
      content: '';
      @extend .#{$icon};
      @extend .icon-20;
    }
