      color: $black;
      vertical-align: top;
    }

    button {
      @extend .font-size-16;
    }
  }
}

/* loading */
@mixin progress($barBg: $bg-color-d, $barColor: $brand-y, $height: 0.3rem) {
  display: block;
  text-align: left;
  overflow: hidden;
  width: 100%;
  height: $height;
  border-radius: $height;
  background: $barBg;

  > span {
    display: block;
    height: 100%;
    background: $barColor;
    border-radius: 0.6rem;
    transition: all $base-duration;
  }
}

