  position: relative;
  animation: loader $duration linear infinite;
  @keyframes loader {
    0% {
      box-shadow: 0 (- circle-normal($size)) 0 0 nth($dot-color, 1),
      circle-angle($size) (- circle-angle($size)) 0 0 nth($dot-color, 2),
      circle-normal($size) 0 0 0 nth($dot-color, 3),
