/* 간격 */
@mixin distance {
  $max: 60;
  $step: 5;

  @for $i from 1 through ($max/$step) {
    $value: $step * $i;
    .mt#{$value} {
      margin-top: $value * 0.1rem !important;
    }
    .mb#{$value} {
      margin-bottom: $value * 0.1rem !important;
    }

    .pt#{$value} {
      padding-top: $value * 0.1rem !important;
    }
    .pb#{$value} {
      padding-bottom: $value * 0.1rem !important;
    }


    @if $value > 15 {
      .mtb#{$value} {
        margin-top: $value * 0.1rem !important;
        margin-bottom: $value * 0.1rem !important;
      }
      .ptb#{$value} {
        padding-top: $value * 0.1rem !important;
        padding-bottom: $value * 0.1rem !important;
      }
    }
  }
  // 예외
  .mt12 {
    margin-top: 1.2rem !important;
  }
  .mb12 {
    margin-bottom: 1.2rem !important;
  }
}

@function circle-angle($size) {
  @return round($size * 0.70710678118) + ($size / 2);
}

@function circle-normal($size) {
  @return round($size * 0.70710678118) + $size;
}